    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.my-photo{
    width: 30%;
    border-radius: 50%;  /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */  /* Firefox 3.5 - 3.6 */
    box-shadow:         2px 3px 5px 6px rgb(231, 112, 15);
}

.info{
